const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

export async function getUser(id) {
    try {
        console.log('getUserCalled');
        const user = doc(db, '/users', '' + id);
        return (await getDoc(user)).data();
    } catch (e) {
        throw (e);
    }
}
export async function createUser(id, payload) {
    // if(payload.type === 'seller'){

    // }
    try {
        console.log({ payload });
        const user = doc(db, '/users', '' + id);
        if ((await getDoc(user)).data()) throw (new Error((payload && payload.type === 'seller' ? 'Seller' : 'User') + ' already exists'));
        return await setDoc(user, payload);
    } catch (e) {
        throw (e);
    }
}



export default db;


